<template>
<div class='sellerOrder'>
<template v-for="orderVo in orderVoList">
 <Card :key="orderVo.orderId">            
                <p>要求送达时间:{{orderVo.orderTime}}</p>
                <p>用户名:{{orderVo.userName}}</p>
                <p>用户备注:{{orderVo.orderRemark}}</p>
                <p>用户手机号:{{orderVo.userPhone}}</p>
                <p>总金额:{{orderVo.allPrice}}</p>
                <p>地址:{{orderVo.userAddress}}</p>
                 <Button type="warning" class='bu' @click="submitcansend(orderVo)" v-if="orderState=='0'">确认接单</Button>
                 <Button type="warning" class='bu' @click="submitcansend(orderVo)" v-if="orderState=='1'">确认送达</Button>
                 <br>
                <template v-for="foodVo in orderVo.foodAndNum">                   
                <a :key="foodVo.foodId" @click="goCanteen(foodVo.canteenId)">
                 {{foodVo.foodName}}   
                </a>
                <b :key="foodVo.foodId">
                  {{foodVo.foodNum}}         
                </b>    
                </template>
            </Card>
</template>
</div>
</template>
<script>
import {selectUserOrder,updateOrderState} from '@/api/api.js'
export default {
 data(){
     return{
         canteenId:this.$store.state.user.canteenId,
         orderState:this.$route.query.orderState,
         orderVoList:[]
     }
 },
 mounted(){
     let data={orderState:this.orderState}
     selectUserOrder(data).then(res=>{
         this.orderVoList=res.data.data
     })
 },
 methods:{
     submitcansend(orderVo){
         let data={orderId:orderVo.orderId,orderState:this.orderState+1}
         updateOrderState(data).then(res=>{
             this.$Message.success('修改成功')
               this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'sellerMain'}) 
                   }, 3); 
         })
     },
 }   
}
</script>
<style lang="less">
.sellerOrder{
    .bu{
        margin:10px 0px 10px 0px;
    }
    p{
        margin-top:10px;
    }
}
</style>
